.ShopToken_row_container:hover{
height: 190px;
}

.ShopToken_row_container{
    width: 870px;
    float: left;
    overflow: hidden;
    height: 90px;
    margin-bottom: 10px;
    border-radius: 25px;
}

.ShopToken_item_row_container:hover{
    height: 670px;
    }
    
    .ShopToken_item_row_container{
        width: 870px;
        float: left;
        overflow: hidden;
        height: 570px;
        margin-bottom: 10px;
        border-radius: 25px;
    }


    
.Shop_Token_item_panel{
    overflow: hidden;
    font-size: 40px;
    height: calc( 100% - 55px);
    padding: 20px;
    border-radius: 20px;
    border-radius: 25px;
    height: calc( 100% - 45px);
    }
    
    .Shop_Token_item_panel_disabled{
    overflow: hidden;
    font-size: 40px;
    height: calc( 100% - 55px);
    padding: 20px;
    border-radius: 20px;
    opacity:0.3;
    border-radius: 25px;
    height: calc( 100% - 45px);
    }

    .ShopToken_item_icon_container{
        width: 47px;
        height: 47px;
        float: left;
        overflow: hidden;
    }

    .ShopToken_item_icon_container:hover{
        width: 120px;
    }
    
    .Shop_Token_itemimage_container{
        width: 300px;
        margin: auto;
    }

.ShopToken_refill_true{
    float: left;
    border-radius: 10px;
    cursor: pointer;
    height: 52px;
}

.ShopToken_refill_true:hover{
    background:white;
}

.ShopToken_refill_false:hover{
    background:white;
}

.Shop_token_itemPanel{

    /* width: 100px; */
    border-radius: 10px;
    margin-top: 15px;
    padding: 10px;
}

.ShopToken_item_image{
    width: 300px;
    margin-left: calc( ( 100% - 300px ) / 2 );
}

.Shop_token_descPanel{
    width: calc( 100% - 25px ); 
    border-radius: 5px;
    margin-top: 15px;
    padding: 10px;
    font-size: 25px;
}

.ShopToken_item_creator{
    font-size: 20px;
}

.ShopToken_creator_label{
    padding-top: 15px;
    padding-right: 10px;
}

.Shop_token_collectionicon{
width: 50px;
border-radius: 3px;
cursor: pointer;
}

.ShopToken_collection_label{
    padding-top: 10px;
    padding-right: 10px;
}

.Shop_token_item_controlPanel{

    border-radius: 5px;
    margin-top: 10px;
    margin-bottom: 5px;
    font-size: 25px;
    height: 47px;
}

.ShopToken_buydrop{
    float: left;
    width: 50px;
    border-radius: 10px;padding-top: 1px;
}

.ShopToken_refill_false{
    float: left;
    cursor: pointer;
    border-radius: 10px;
    height: 52px;
}

.ShopToken_product_tool{
    float: right;
    width: 70px;
}

.ShopToken_row{
   height: 100%;
    width: 800px;
    margin-bottom: 10px;
}

.ShopToken_row2:hover{
 height: 105px;
    width: 800px;
    margin-bottom: 10px;
}

.ShopToken_item_icon{
    float: left;
    width: 47px;
    height: 47px;
    cursor:pointer;
    margin-right: 10px;
}

.feed_link{
    cursor: pointer;
    float: left;
    border-radius: 5px;
    height: 47px;
    overflow: hidden;
}


.ShopToken_item_label{
    float: left;
    margin-top: 0px;
    margin-left: 0px;
}

.ShopToken_item_price_icon{
    float: right;
    width: 50px;
}

.ShopToken_item_price{
    margin-right: 10px;
}


.ShopToken_item_setprice{
    width: calc( 100% - 10px );
    height: 60px;
    border-radius: 10px;
    margin-top: 20px;
    padding: 5px;
}


.ShopToken_item_setprice_field{
    font-size: 32px;

    background: #67bfd9;
    border: solid 0px;
    margin-left: 5px;
    width: 490px;
    margin-top: 5px;
    padding: 7px;
    padding-left: 10px;
}

.ShopToken_item_setprice_button{
    cursor: pointer;
    margin: auto;
    padding: 5px;
    font-size: 32px;
    background: #0788ae;
    border-radius: 7px;
    padding-right: 20px;
    padding-left: 15px;
    float: right;
    height: 43px;
}

.ShopToken_item_openitems_button{
    cursor: pointer;
    padding: 5px;
    font-size: 25px;
    background: #0788ae;
    border-radius: 7px;
    padding-left: 10px;
    padding-right: 10px;
    float: left;
    margin-right: 10px;
}

.button_label_left{
float: left;
padding-top: 2px;
padding-right: 10px;
}

.button_label_right{
    float: right;
    padding-top: 2px;
    padding-left: 10px;
}

.ShopToken_item_setprice_button:hover{
background: white;
color: green;
}


.ShopToken_item_openitems_button:hover{
    background: white;
    color: green;
    }

.ShopToken_item_setamount_field{
    font-size: 32px;  
    background: #67bfd9;
    border: solid 0px;
    margin-left: 5px;
    width: 180px;
    float: left;
    margin-top: 7px;
    text-align: right;
    padding-right: 10px;
}