
.Ticket_Modules{
    cursor: pointer;
    overflow: hidden;
    font-size: 40px;
    padding: 10px;
    border-radius: 10px;
    width: calc( 100% - 26px );
    margin-top: 5px;
    height: 54px;
}
    
.Ticket_Modules_disabled{
    overflow: hidden;
    font-size: 40px;
    padding: 10px;
    border-radius: 10px;
    width: calc( 100% - 26px );
    margin-top: 5px;
    height: 54px;
    opacity: 0.5;
}
   
.Ticket_Modules:hover{
    border: solid 3px white;
    height: 170px;
}
 
    
.Ticket_Modules_disabled:hover{
    border: solid 3px white;
    height: 170px;
}
    
.Ticket_desc{

    font-style: italic;
    width: 100%;
}

.Ticket_mini_banner{
    float: right;
    height: 65px;
    border-radius: 10px;
    width: 260px;
    background: #0788ae;
    margin-top: -6px;
    margin-right: -6px;
    background-size:100% 100%;
}

.Ticket_module_icon{
    float: left;
    margin-right: 10px;
    margin-bottom: 10px;
}
    
.Ticket_module_install_button{
        opacity: 1;
        cursor: pointer;
        margin: auto;
        padding: 5px;
        font-size: 32px;
        border-radius: 7px;
        padding-right: 20px;
        padding-left: 15px;
        float: right;
        height: 43px;
}
    
.Ticket_module_install_button:hover{
    background: white;
    color: green;
    opacity: 1;
}